import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  max-width: 800px;
  position: relative;
  display: block;
  margin-bottom: 15px;
  width: 100%;
  background: #eee;
  height: 35px;
  border-radius: 3px;
  transition: 0.4s linear;
  transition-property: width, background-color;
`;
const Title = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  font-weight: bold;
  font-size: 13px;
  color: #ffffff;
  background: ${props =>
    (props.background === "skill_1" && "deeppink") ||
    (props.background === "skill_2" && "coral") ||
    (props.background === "skill_3" && "SlateBlue") ||
    (props.background === "skill_4" && "DeepSkyBlue") ||
    (props.background === "skill_5" && "tomato")};
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
`;
const Name = styled.span`
  display: block;
  width: 110px;
  background: rgba(0, 0, 0, 0.1);
  padding: 0 20px;
  height: 35px;
  line-height: 35px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
`;
const Bar = styled.div`
  height: 35px;
  width: 0px;
  background: #6adcfa;
  border-radius: 3px;
`;
const Percentage = styled.div`
  position: absolute;
  right: 10px;
  top: 0;
  font-size: 11px;
  height: 35px;
  line-height: 35px;
  color: #ffffff;
  color: rgba(0, 0, 0, 0.4);
`;

function Skillbar({ className, name, score, background }) {
  console.log("className", className);
  return (
    <React.Fragment>
      <Wrapper className={className} data-percent="20%">
        <Title background={background}>
          <Name>{name}</Name>
        </Title>
        <Bar style={{ background: "#e67e22" }} />
        <Percentage>{score}</Percentage>
      </Wrapper>
    </React.Fragment>
  );
}

export default Skillbar;
